.cart-wrapper {
	position: fixed;
	right: 0;
	top: 0;
	width: 100vw;
	height: 100vh;
	background: rgba(0, 0, 0, 0.2);
	opacity: 0;
	transition: opacity 0.5s ease;
	z-index: 0;
	display: flex;
	justify-content: flex-end;

	.cart-drawer {
		width: 30%;
		height: 100%;
		background: #fff;
		box-shadow: -2px 0 6px rgba(0, 0, 0, 0.15);
		padding: 0px 20px;
		display: flex;
		flex-direction: column;
		transform: translateX(100%);
		transition: transform 0.3s ease;

		&__header {
			display: flex;
			justify-content: space-between;
			padding: 35px 0px 15px;
			border-bottom: 1px solid rgba($black-primary, 0.1);

			.cart__title {
				font-weight: 600;
				font-size: 24px;
				line-height: 108%;
				letter-spacing: 0.02em;
				color: $black-primary;
			}
		}

		&__items {
			display: flex;
			flex-direction: column;
			gap: 24px;
			padding-top: 40px;
			flex-grow: 1;
			overflow-y: auto;

			.cart-item {
				display: flex;
				gap: 20px;
				align-items: flex-start;
				padding-bottom: 20px;
				border-bottom: 1px solid $grey;

				&__image {
					flex: 0 0 35%;
					position: relative;

					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}

					.badge {
						position: absolute;
						font-weight: 400;
						font-size: 12px;
						line-height: 167%;
						color: $black-primary;
						padding: 2px 10px;
						background-color: #fff;
						left: 10px;
						top: 10px;
					}
				}

				&__info {
					display: flex;
					flex-direction: column;
					flex-grow: 1;
					align-items: flex-start;

					.cart-item__title {
						font-weight: 300;
						font-size: 16px;
						line-height: 150%;
						letter-spacing: 0.02em;
						text-align: center;
						color: #3a3836;
					}

					.cart-item__price {
						font-weight: 600;
						font-size: 16px;
						line-height: 150%;
						letter-spacing: 0.02em;
						text-align: center;
						color: #3a3836;
					}

					.cart-item__variant {
						font-weight: 300;
						font-size: 14px;
						line-height: 157%;
						letter-spacing: 0.02em;
						text-align: center;
						color: $black-primary;

						span {
							color: $dark-grey-secondary;
						}
					}

					.cart-item__qty {
						display: flex;
						align-items: center;
						gap: 10px;
						width: 100%;
						border: 1px solid #d8d8d8;
						width: fit-content;

						button {
							padding: 4px 15px 4px 10px;
							height: 100%;
							font-weight: 400;
							font-size: 16px;
							line-height: 169%;
							text-align: center;
							color: #707070;
						}

						input {
							font-weight: 400;
							font-size: 16px;
							line-height: 169%;
							text-align: center;
							color: #707070;
							height: 100%;
							border: none;
							flex-grow: 1;
							flex-shrink: 1;
							max-width: 30px;
							-moz-appearance: textfield;

							&::-webkit-outer-spin-button,
							&::-webkit-inner-spin-button {
								-webkit-appearance: none;
								margin: 0;
							}
						}
					}
				}
			}
		}

		&__footer {
			.cart-subtotal {
				display: flex;
				align-items: center;
				justify-content: space-between;

				span {
					font-weight: 600;
					font-size: 24px;
					line-height: 108%;
					letter-spacing: 0.02em;
					color: $black-primary;
				}
			}

			.terms {
				display: flex;
				align-items: center;
				gap: 10px;

				&__label {
					font-weight: 400;
					font-size: 14px;
					line-height: 157%;
					color: $black-primary;

					a {
						color: $black-primary;
						text-decoration: underline;
					}
				}
			}
		}
	}

	&.open {
		z-index: 999;
		opacity: 1;

		.cart-drawer {
			transform: translateX(0);
		}
	}
}

.cart-subtotal {
	display: flex;
	justify-content: space-between;
	font-weight: bold;
	margin: 15px 0;
}

.btn-checkout {
	width: 100%;
	background: black;
	color: white;
	padding: 12px;
	border: none;
	cursor: pointer;
}
