.catalog-page__container {
	.catalog-page__header {
		display: flex;
		justify-content: space-between;
		margin-top: 40px;

		.search-form {
			display: flex;
			align-items: center;
			border: 1px solid #d8d8d8;
			flex: 0 0 21.5%; /* 324/1506 */

			@media (max-width: 1280px) {
				flex: 0 0 26%;
			}

			input {
				padding: 15px 0px 15px 15px;
				font-weight: 400;
				font-size: 14px;
				line-height: 157%;
				color: $black-primary;
				border: none;
				outline: none;
				flex-grow: 1;

				&::placeholder {
					color: $dark-grey-secondary;
				}
			}

			button {
				display: flex;
				align-items: center;
				justify-content: center;
				height: 100%;
				padding: 15px;
			}
		}

		.sort-by-select {
			font-weight: 400;
			font-size: 16px;
			line-height: 150%;
			color: $dark-grey-secondary;
			padding: 15px;
			border: 1px solid #d8d8d8;
		}
	}

	.catalog-page__content {
		display: flex;
		align-items: flex-start;
		gap: 60px;
		margin-top: 30px;
		margin-bottom: 120px;

		.filters-block {
			flex: 0 0 21.5%; /* 324/1506 */

			@media (max-width: 1280px) {
				flex: 0 0 26%;
			}
			.submit-filters {
				width: 100%;
			}

			.filters__list {
				display: flex;
				flex-direction: column;
				gap: 20px;

				.filter__item {
					display: flex;
					flex-direction: column;
					border-bottom: 1px solid #d8d8d8;
					gap: 20px;
					padding-bottom: 20px;

					.filter__title {
						font-size: 16px;
						line-height: 150%;
						letter-spacing: 0.02em;
						color: $black-primary;
					}

					.price-range {
						display: flex;
						align-items: center;
						gap: 20px;

						input {
							flex-grow: 1;
							padding: 15px;
							border: 1px solid $black-primary;
							color: $black-primary;

							&::placeholder {
								color: $dark-grey-secondary;
							}
						}

						.separator {
							font-size: 18px;
							font-weight: 600;
							color: $black-primary;
						}
					}

					.filter__values {
						display: flex;
						flex-direction: column;
						gap: 20px;

						.filter__value {
							display: flex;
							align-items: center;
							gap: 11px;
						}

						label {
							font-weight: 400;
							font-size: 14px;
							line-height: 157%;
							color: $black-primary;
						}

						input {
							border: 1px solid #d8d8d8;
							color: $black-primary;
							width: 24px;
							height: 24px;
							border-radius: 0px;
							outline: none;
						}
					}
				}
			}
		}

		.products-list {
			display: grid;
			grid-template-columns: repeat(3, 1fr);
			column-gap: 30px;
			row-gap: 40px;

			.product-list__item {
				display: flex;
				flex-direction: column;

				.image__block {
					width: 100%;
					max-width: 100%;
					aspect-ratio: 354 / 441;
					display: flex;
					align-items: center;
					justify-content: center;
					position: relative;
					overflow: hidden;

					.product__label {
						position: absolute;
						left: 20px;
						top: 20px;
						font-weight: 400;
						font-size: 12px;
						line-height: 167%;
						padding: 2px 10px;
						background-color: $white;
						color: $black-primary;

						@media (max-width: 980px) {
							top: 10px;
							left: 16px;
						}

						@media (max-width: 768px) {
							font-size: 10px;
							padding: 0px 6px;
						}
					}

					.view-more {
						position: absolute;
						bottom: 0;
						left: 0;
						width: 100%;
						font-weight: 400;
						font-size: 16px;
						line-height: 150%;
						letter-spacing: 0.02em;
						color: $dark-grey-secondary;
						background-color: rgba(255, 255, 255, 0.7);
						padding: 20px 0px;
						text-align: center;
						transform: translateY(100%);
						transition: transform 0.5s ease;
					}

					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}

					&:hover {
						.view-more {
							transform: translateY(0);
						}
					}
				}

				.product__name {
					padding: 10px 0px;
					font-family: $font-family;
					font-weight: 300;
					font-size: 16px;
					line-height: 150%;
					letter-spacing: 0.02em;
					text-align: center;
					color: #3a3836;

					@media (max-width: 980px) {
						font-size: 14px;
						padding: 6px 0px;
					}

					@media (max-width: 467px) {
						font-size: 12px;
						padding: 2px 0px;
					}
				}

				.product__price {
					font-family: $font-family;
					font-weight: 600;
					font-size: 16px;
					line-height: 150%;
					letter-spacing: 0.02em;
					text-align: center;
					color: #3a3836;
					display: flex;
					align-items: center;
					justify-content: center;
					gap: 12px;

					@media (max-width: 980px) {
						font-size: 14px;
					}

					@media (max-width: 467px) {
						font-size: 12px;
					}

					.old-price {
						font-family: $font-family;
						font-weight: 400;
						font-size: 12px;
						line-height: 183%;
						text-decoration: line-through;
						text-align: center;
						color: $red-primary;

						@media (max-width: 980px) {
							font-size: 10px;
						}

						@media (max-width: 467px) {
							font-size: 9px;
						}
					}
				}
			}
		}

		.pagination {
			display: flex;
			align-items: center;
			gap: 8px;
			padding-top: 80px;
			justify-content: center;

			.pages-link {
				width: 40px;
				height: 40px;
				border: 1px solid #d8d8d8;
				font-weight: 400;
				font-size: 14px;
				line-height: 157%;
				color: $black-primary;
				display: flex;
				align-items: center;
				justify-content: center;
				transition: border 0.3s ease;

				&.previous {
					svg {
						transform: rotate(180deg);
					}
				}

				&:hover {
					border: 1px solid #a5a5a5;
				}
			}

			.current-page {
				width: 40px;
				height: 40px;
				border: 1px solid #d8d8d8;
				font-weight: 400;
				font-size: 14px;
				line-height: 157%;
				color: $white;
				display: flex;
				align-items: center;
				justify-content: center;
				background-color: $black-primary;
			}
		}
	}

	.catalog-page__bottom {
		margin-bottom: 120px;

		.bottom__title {
			font-weight: 600;
			font-size: 60px;
			line-height: 117%;
			letter-spacing: 0.02em;
			color: $black-primary;
			margin-bottom: 120px;
		}

		.newsletter__block {
			display: flex;
			align-items: center;

			img {
				aspect-ratio: 753/493;
				width: 100%;
				height: auto;
			}

			.newsletter__info {
				overflow: hidden;
				width: 100%;
				height: 100%;

				&.gift-card__content {
					padding: 94px 0px 0px 80px;
					display: flex;
					flex-direction: column;
					gap: 20px;
					align-items: flex-start;
					overflow: visible;

					@media (max-width: 1280px) {
						padding: 48px 0px 0px 48px;
						gap: 16px;
					}

					@media (max-width: 1110px) {
						padding: 16px 0px 0px 32px;
						gap: 12px;

						.gift-card__title {
							font-size: 28px;
						}
					}

					@media (max-width: 768px) {
						padding: 30px 0px;
					}

					@media (max-width: 468px) {
						padding: 30px 15px;

						.gift-card__title {
							font-size: 24px;
						}
					}

					.gift-card__desc {
						font-weight: 400;
						font-size: 14px;
						line-height: 157%;
						color: $dark-grey-secondary;

						@media (max-width: 830px) {
							font-size: 12px;
						}

						@media (max-width: 768px) {
							font-size: 14px;
						}
					}

					.gift-card__buy-form {
						display: flex;
						flex-direction: column;
						gap: 20px;

						@media (max-width: 1280px) {
							gap: 16px;
						}

						@media (max-width: 1110px) {
							gap: 12px;
						}

						@media (max-width: 980px) {
							width: 100%;
							display: grid;
							grid-template-columns: repeat(2, 1fr);
							gap: 16px;
						}

						@media (max-width: 467px) {
							grid-template-columns: 1fr;
							gap: 10px;
						}
					}

					.gift-card__price {
						position: relative;

						.price__dropdown {
							position: absolute;
							top: calc(100% + 12px);
							border: 1px solid #d8d8d8;
							padding: 14px 7px;
							width: 100%;
							display: flex;
							flex-direction: column;
							gap: 7px;
							background-color: $white;
							max-height: 0px;
							overflow: hidden;
							opacity: 0;
							transition: all 0.5s ease;

							&.opened {
								opacity: 1;
							}

							.price__item {
								button {
									text-align: start;
									width: 100%;
									padding: 4px 0px 4px 14px;
									font-family: $font-family;
									font-weight: 300;
									font-size: 14px;
									line-height: 157%;
									color: $dark-grey-secondary;
									transition: all 0.5s ease;

									&:hover {
										color: $black-primary;
										background-color: $light-grey;
									}
								}

								&.active button {
									background-color: $black-primary;
									color: $white;

									&:hover {
										background-color: $black-primary;
										color: $white;
									}
								}
							}
						}

						.price__button {
							display: flex;
							align-items: center;
							justify-content: space-between;
							width: 210px;
							border: 1px solid #d8d8d8;
							padding: 9px 15px;
							width: 240px;
							font-family: $font-family;
							font-weight: 300;
							font-size: 14px;
							line-height: 157%;
							color: $black-primary;

							@media (max-width: 1440px) {
								padding: 7px 12px;
							}

							@media (max-width: 980px) {
								width: 100%;
								padding: 12px;
							}

							svg {
								transition: transform 0.5s ease;
							}

							&.active svg {
								transform: rotate(-180deg);
							}
						}
					}

					.gift-card__buy-now {
						width: 240px;

						@media (max-width: 1110px) {
							padding: 14px 0px;
						}

						@media (max-width: 980px) {
							width: auto;
						}
					}
				}

				&.sale__info {
					display: flex;
					flex-direction: column;
					gap: 20px;
					justify-content: center;
					padding: 0px 80px;
					align-items: flex-start;

					@media (max-width: 1280px) {
						padding: 0px 48px;
						gap: 16px;
					}

					@media (max-width: 1110px) {
						padding: 0px 32px;
						gap: 12px;

						.sale-info__title {
							font-size: 28px;
						}

						.sale-info__shop-now {
							padding: 14px 64px;
						}
					}

					@media (max-width: 768px) {
						padding: 30px 0px;
					}

					@media (max-width: 468px) {
						padding: 30px 15px;

						.sale-info__title {
							font-size: 24px;
						}

						.sale-info__shop-now {
							width: 100%;
						}
					}

					.sale-info__desc {
						font-weight: 400;
						font-size: 14px;
						line-height: 157%;
						color: $dark-grey-secondary;

						@media (max-width: 830px) {
							font-size: 12px;
						}

						@media (max-width: 768px) {
							font-size: 14px;
						}
					}
				}

				&.newsletter__info {
					display: flex;
					flex-direction: column;
					gap: 20px;
					justify-content: center;
					padding: 0px 80px;
					align-items: flex-start;

					@media (max-width: 1280px) {
						padding: 0px 48px;
						gap: 16px;
					}

					@media (max-width: 1110px) {
						padding: 0px 32px;
						gap: 12px;

						.newsletter__title {
							font-size: 28px;
						}

						.newsletter__submit {
							padding: 14px 64px;
						}
					}

					@media (max-width: 768px) {
						padding: 30px 0px;
					}

					@media (max-width: 468px) {
						padding: 30px 15px;

						.newsletter__title {
							font-size: 24px;
						}
					}

					.newsletter__desc {
						font-weight: 400;
						font-size: 14px;
						line-height: 157%;
						color: $dark-grey-secondary;

						@media (max-width: 830px) {
							font-size: 12px;
						}

						@media (max-width: 768px) {
							font-size: 14px;
						}
					}

					.newsletter__form {
						display: flex;
						gap: 20px;
						flex-direction: column;
						align-items: flex-start;

						@media (max-width: 468px) {
							width: 100%;
						}

						input {
							padding: 14px 15px;
							border: 1px solid #d8d8d8;
							font-weight: 400;
							font-size: 16px;
							line-height: 150%;
							color: $black-primary;
							width: 100%;

							&::placeholder {
								color: $dark-grey-secondary;
							}
						}

						.newsletter__submit {
							width: 100%;
						}
					}
				}
			}
		}
	}
}
